import React from 'react';
import ReactDOM from 'react-dom';
import './styles/app.less';
import registerServiceWorker from './registerServiceWorker';
import { applyMiddleware, createStore} from 'redux';
import reducers from './js/redux/index'
import { Provider } from 'react-redux';
import Dongtai from './js/containers/dongtai/index'
import Carousel from './js/components/Carousel/index'
import thunk from 'redux-thunk';
import {HashRouter as Router,Route,Link} from 'react-router-dom'

const store = createStore(reducers, applyMiddleware(thunk));

const Root = () => (
    <Router>
        <div>
            {/*<ul>*/}
                {/*<li><Link to="/">Dongtai</Link></li>*/}
                {/*<li><Link to="/circle">Quanzi</Link></li>*/}
            {/*</ul>*/}

            {/*<hr/>*/}

            <Route exact path="/" component={Dongtai}/>
            <Route exact path="/carousel" component={Carousel}/>
        </div>
    </Router>
)

ReactDOM.render(
    <Provider store={store}>
        <Root/>
    </Provider>,
    document.getElementById('root'));
registerServiceWorker();



