import React,{PureComponent} from 'react'
import {Button} from 'antd-mobile'
import './index.less'

class Comment extends PureComponent{

    render(){
        const{show, commentStr, onSend, onChange} = this.props;
        let commentClass = "";
        if(show){
            commentClass = "dongtai-footer-container comment-show";
        }else
            commentClass = "dongtai-footer-container comment-hide";

        console.log(commentClass);

        return(
            <div className={commentClass}>
                <div className="footer-container2">
                    <div className="comment-container">
                        <input value={commentStr} onChange={onChange}  className="font-26 input-comment" type="text"  placeholder="..." />
                        {/*<img className="emoji" src={emoji} onClick={this._toggleEmoji}/>*/}
                        <Button size="small" type="primary" onClick={onSend}></Button>
                    </div>
                </div>
            </div>
        );
    }

}

export default Comment;



