export function getUserInfo(){
    if(window.localStorage){
        // console.log("" + eval('(' + localStorage.getItem("userInfo") + ')'));
        return eval('(' + localStorage.getItem("userInfo") + ')');
    }else{
        alert(" web storage ");
    }
}

export function getUserToken(){
    if(window.localStorage){
        const obj = eval('(' + localStorage.getItem("userInfo") + ')');
        if(obj && obj.Accesstoken)
            return obj.Accesstoken;
        else
            // return "";
            return "8MJ6BKL9436Ncdaefe35-6487-4138-ada9-d8776d06848eX1T7WE9432Se10adc3949ba59abbe56e057f20f883eA7Z7BC17RIE0HD67TQW7EE740OGB91K804";
    }else{
        alert(" web storage ");
    }
}

export function getName(){
    if(window.localStorage){
        const obj = eval('(' + localStorage.getItem("userInfo") + ')');
        if(obj && obj.name)
            return obj.name;
        else
            return "";
    }else{
        alert(" web storage ");
    }
}

export function getUserName(){
    if(window.localStorage){
        const obj = eval('(' + localStorage.getItem("userInfo") + ')');
        if(obj && obj.username)
            return obj.username;
        else
            return "";
    }else{
        alert(" web storage ");
    }
}

/**
 * item
 * obj
 * @param item
 */
export function saveQuanItem(item){
    if(window.localStorage){
        var objStr = JSON.stringify(item);
        // console.log("" + objStr);
        localStorage.setItem("quanItem",objStr);
    }else{
        alert(" web storage ");
    }
}
/**
 * item
 * @returns {Object}
 * {username ,Accesstoken, creationdate,modificationdate ,telephone , email, varCard }
 */
export function getQuanItem(){
    if(window.localStorage){
        return eval('(' + localStorage.getItem("quanItem") + ')');
    }else{
        alert(" web storage ");
    }
}

/**
 * item
 */
export function clearQuanItem() {
    localStorage.removeItem("quanItem");
}







