import React, {PureComponent} from 'react'
import { Carousel, WhiteSpace, WingBlank,Icon } from 'antd-mobile';
import './index.less';
import {LocalStorageUtil} from '../../util/index';
import {createHashHistory} from 'history'

const history = createHashHistory();
class CarouselView extends PureComponent {

    _back= ()=>{
        const {actions} = this.props;
        actions.hideCarousel();
    }

    render() {
        const {state} = this.props;
        if(state.showCarousel){
            const hProp = { height:  document.documentElement.clientHeight};
            const arr = state.imgArr;
            return (
                <div className="carousel-container" style={hProp}>

                        <div className="back" onClick={this._back}>
                            <Icon color="#fff" type="left"/>
                            <span></span>
                        </div>

                    <Carousel
                        className="my-carousel"
                        autoplay={false}
                        infinite
                        selectedIndex={state.imgIndex}
                        swipeSpeed={35}
                        beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                        afterChange={index => console.log('slide to', index)}
                    >
                        {arr.map((item, index) => (
                            <div  key={index} >
                                <img
                                    // src={'data:image/jpeg;base64,' + item}
                                    src={item}
                                    alt="icon"
                                    onLoad={() => {
                                        // fire window resize event to change height
                                        window.dispatchEvent(new Event('resize'));
                                    }}
                                />
                            </div>
                        ))}
                    </Carousel>


                </div>
            );
        }else {
            return(<div></div>);
        }

    }
}


export default CarouselView;



