import React, { PureComponent } from 'react'
import './index.less'
import Divider from '../Divider'
import {tool, LocalStorageUtil} from '../../util/index'
import comment from '../../../imgs/dongtai/comment.png'
import xiaoxi from '../../../imgs/dongtai/xiaoxi.png'
import zan from '../../../imgs/dongtai/zan.png'
import unzan from '../../../imgs/dongtai/unzan.png'
import touxiang from '../../../imgs/dongtai/touxiang.png'
import {Natives} from '../../constants/index'
import {createHashHistory} from 'history'


const history = createHashHistory();


class DongtaiItem extends PureComponent{

    _carousel= (imgs, index)=>{
        try {
            console.log(imgs);
            // native
            window.dsBridge.call(Natives.SHOW_BIG_IMGS_FROM_JS, {imgs: imgs, index: index}, function (responseData) {
                console.log("callback from native-->" + responseData);
            });
        } catch (error) {
            console.log(error)
        }
        // this.props.actions.showCarousel(imgArr, index);
    }
    _renderHead= ()=>{
        const {vcard} = this.props.item;
        if(vcard){
            var obj = eval('(' + vcard + ')');
            if(obj.vCard && obj.vCard.headimg){
                let head = 'data:image/jpeg;base64,' + obj.vCard.headimg;
                return(
                    <img className="item-head" src={head}/>
                );
            }
        }
        return(
            <img className="item-head" src={touxiang}/>
        );

    }

    _renderPic= ()=>{
        let {imagestr} = this.props.item;
        // 
        if(imagestr && imagestr.length>0){
            if(imagestr.lastIndexOf(",") == (imagestr.length -1)){
                imagestr = imagestr.substring(0, imagestr.length -1);
            }
            const arr = imagestr.split(",");

            let picViews = [];
            arr.map((item, index)=>{
                if(index<4){
                    let picView = (
                        <div className="img-container" key={index}
                             onClick={()=>{this._carousel(imagestr, index)}}
                        >
                            <img className="img-4" src={item}></img>
                        </div>
                    )
                    picViews.push(picView);
                }
            })
            return(
                <div className="pic-container">
                    {picViews}
                </div>
            );
        }
    }

    _renderLike = ()=>{
        const {thumbsups} = this.props.item;
        let likes = '';
        if(thumbsups && thumbsups.length>0){
            thumbsups.map((item, index)=>{
                if(index < 3){
                    likes += item.name + ",";
                }
            })
            if(likes.lastIndexOf(",") == (likes.length -1)){
                likes = likes.substring(0, likes.length -1);
            }
            if(thumbsups.length < 4)
                likes+="";
            else
                likes+=""+thumbsups.length+"";

            return(
                <div>
                    <Divider style={{margin: "0.12rem 0"}}/>
                    <span className="font-26 color-blue likes-style">{likes}</span>
                </div>
            );
        }
    }

    _checkZan = ()=>{
        const {thumbsups} = this.props.item;
        let iszan = false;
        if(thumbsups && thumbsups.length>0){
            thumbsups.map((item)=> {
                if(item.token === LocalStorageUtil.getUserToken())
                    iszan = true;
                else
                    iszan = false;
            })
        }
        if(iszan){
            return(
                <div className="action-1" onClick={this._unzan}>
                    <img src={zan}/>
                    <span className="font-26 color-blue"></span>
                </div>
            )
        }else {
            return(
                <div className="action-1" onClick={this._zan}>
                    <img src={unzan}/>
                    <span className="font-26 color-999"></span>
                </div>
            );
        }
    }

    _renderComment = ()=>{
        const {comment} = this.props.item;
        if(comment && comment.length>0){
            let commentViews = [];
            comment.map((item, index)=>{
                let commentView = (
                    <div key={index}>
                        <span className="font-26 color-blue" style={{marginRight:'0.12rem'}}>{item.name}:</span>
                        <span className="font-26 color-333">{item.contect}</span>
                    </div>
                )
                commentViews.push(commentView);
            })

            return(
                <div  className="like-container">
                    {commentViews}
                </div>

            );
        }
    }

    _zan = (event)=>{
        const {item, actions} = this.props;
        event.stopPropagation();
        console.log("zan");
        actions.addCommentOrLike({
            trendsid: item.trendsid,
            token: LocalStorageUtil.getUserToken(),
            name: LocalStorageUtil.getName().length===0 ? LocalStorageUtil.getUserName():LocalStorageUtil.getName(),
            type: 0,
        });
    }

    _unzan = (event)=>{
        const {item, actions} = this.props;
        event.stopPropagation();
        console.log("unzan");
        actions.cancleCommentOrLike({
            trendsid: item.trendsid,
            token: LocalStorageUtil.getUserToken(),
            name: LocalStorageUtil.getName().length===0 ? LocalStorageUtil.getUserName():LocalStorageUtil.getName(),
            type: 0,
        });
    }

    _toggleComment = (event)=>{
        const {item, actions} = this.props;
        event.stopPropagation();
        actions.toggleComment(item);
    }

    _chat= (event)=>{
        const {item} = this.props;
        event.stopPropagation();

        let name, type;
        // 
        if(item.type == "1"){
            name = item.circlename;
            type = 1;
        } else{
            name = item.username;
            type = 0;
        }
        try {
            // native
            window.dsBridge.call(Natives.CHAT_FROM_JS, {name: name, type: type}, function (responseData) {
                console.log("callback from native-->" + responseData);
            });
        }catch (error){
            console.log(error)
        }
    }

    render() {
        const {state, actions, item, onItemClick} = this.props;
        const { name, content, type, thumbsups, posttime} = item;

        return (
            <div className="dongtai-item-container" onClick={onItemClick}>
                <header className="top">
                    {/*<div className="head-container">*/}
                        {/*{this._renderHead()}*/}
                    {/*</div>*/}
                    {this._renderHead()}

                    <div className="top-right">
                        <span className="font-36 color-000">{name}</span>
                        <div>
                            <span className="font-26 color-aaa">{tool.getAgoTime(posttime)}</span>
                            <span className="font-26 color-aaa" style={{marginLeft: '0.12rem'}}>{type == 0? "" : ""}</span>
                        </div>
                    </div>
                </header>

                <main>
                    {/**/}
                    <p className="font-30 color-333 wrap" style={{marginTop: '0.2rem'}}>{content}</p>
                    {/**/}
                    {this._renderPic()}
                    {/**/}
                    {this._renderLike()}
                    {/**/}
                    {this._renderComment()}
                </main>

                <footer>
                    <Divider style={{margin: "0.12rem 0"}}/>
                    <div className="action-3">
                        <div className="action-1">
                            <img src={xiaoxi} style={{width:'0.28rem'}}/>
                            <span className="font-26 color-999" onClick={this._chat}></span>
                        </div>
                        <span className="action-divider">|</span>
                        <div className="action-1" onClick={this._toggleComment}>
                            <img src={comment}/>
                            <span className="font-26 color-999"></span>
                        </div>
                        <span className="action-divider">|</span>
                        {this._checkZan()}
                    </div>
                </footer>
            </div>
        );
    }

}

export default DongtaiItem;


