import {
    REFRESH_ING,
    LOADING_ING,
    GET_ALL_TRENDS,
    LOAD_TRENDS,
    TEST,
    GET_LIKE,
    GET_COMMENT,
    TOGGLE_COMMENT,
    CHANGE_COMMENT,
    ERROR,
    CLEAR_ALL,
    SHOW_CAROUSEL,
    HIDE_CAROUSEL,
    CHANGE_QUAN,
    CHANGE_QUAN_CHENGYUAN,
} from './ActionsTypes'
import 'whatwg-fetch'
import {Toast} from 'antd-mobile'
import {APIs} from '../constants/index'
import {FetchUtil, LocalStorageUtil} from '../util/index'


export const refreshIng = () => ({ type: REFRESH_ING })

export const loadIng = () => ({ type: LOADING_ING })
export const test = () => ({ type: TEST })
export const clearAll = () => ({ type: CLEAR_ALL })
export const hideCarousel = () => ({ type: HIDE_CAROUSEL })


export function toggleComment(item) {
    return({
        type: TOGGLE_COMMENT,
        res:{
            selectedItem: item,
        }}
        );

}
export function showCarousel(imgArr, index) {
    return({
        type: SHOW_CAROUSEL,
        res:{
            imgArr: imgArr,
            imgIndex: index,
        }}
        );

}
export const changeComment = (commentStr) => (
    {
        type: CHANGE_COMMENT ,
        res:{
            commentStr: commentStr,
        }
    }
)
export const refreshQuan = (quan) => (
    {
        type: CHANGE_QUAN ,
        res:{
            quan: quan,
        }
    }
)

export const getAllTrends = (isLoad, reqPage, type, roomID) => {
    console.log("getAllTrends");
    console.log("isLoad-->"+isLoad);

    const reqType = type? type : 0;
    let URL = "";
    switch (reqType) {
        case 0:
            URL = APIs.GET_ALL_TRENDS;
            break;
        case 1:
            URL = APIs.GET_CIRCLE_TRENDS;
            console.log(roomID);
            break;
    }

    console.log(URL);

    return (dispatch, getState) =>{
        const req = {
            page: 1,
            rows: getState().dongtai.size,
            // q: reqType===0?LocalStorageUtil.getUserToken() : LocalStorageUtil.getQuanItem().roomID,
            q: reqType===0?LocalStorageUtil.getUserToken() : roomID,
        };

        console.log(req);
        return fetch(URL,FetchUtil.getPostReq(req))
            .then((response) => response.json())
            .then((res) => {
                // console.log(res);
                const {status, message, results} = res;
                if(status === 100){
                    if(isLoad){
                        dispatch({
                            type: LOAD_TRENDS,
                            res:  {
                                pageSize: reqPage,
                                rows: results.rows,
                            },
                        });
                    }else {
                        const {total, rows} = results;
                        dispatch({
                            type: GET_ALL_TRENDS,
                            res: {
                                total: total,
                                rows: rows,
                            }
                        });
                    }
                }else {
                    Toast.fail(message, 1);
                    dispatch({type: ERROR});
                }
            })
            .catch((error) => {
                Toast.fail(error, 1);
                dispatch({type: ERROR});
            })
    }
}

/**
 * 
 * @param type   1 0
 */
export function addCommentOrLike(req) {
    Toast.loading("...");
    return (dispatch) =>{
        return fetch(APIs.ADD_COMMENT_LIKE, FetchUtil.getPostReq(req))
            .then((response) => response.json())
            .then((res) => {
                console.log(res);
                const {status, message} = res;
                if(status === 100){
                    Toast.success(message, 1);
                    getCommentOrLike(req, dispatch);
                }else {
                    Toast.fail(message, 1);
                }
            })
            .catch((error) => {
                Toast.fail(error, 1);
            })
    }
}

/**
 * 
 * @param type   0 1
 */
export function cancleCommentOrLike(req) {
    Toast.loading("...");
    return (dispatch) =>{
        return fetch(APIs.CANCLE_COMMENT_LIKE, FetchUtil.getPostReq(req))
            .then((response) => response.json())
            .then((res) => {
                console.log(res);
                const {status, message} = res;
                if(status === 100){
                    Toast.success(message, 1);
                    getCommentOrLike(req, dispatch);
                }else {
                    Toast.fail(message, 1);
                }
            })
            .catch((error) => {
                Toast.fail(error, 1);
            })
    }
}

/**
 * 
 */
function getCommentOrLike(req, dispatch) {
    fetch(FetchUtil.getGetReq(APIs.GET_COMMENT_LIKE, {
        trendsid: req.trendsid,
        type: req.type,
    }))
        .then((response) => response.json())
        .then((res) => {
            console.log(res);
            const {status, message, results} = res;
            if(status === 100){
                if(req.type === 0){ //
                    dispatch({
                        type: GET_LIKE,
                        res: {
                            trendsid: req.trendsid,
                            thumbsups: results,
                        }
                    });
                }else {
                    dispatch({
                        type: GET_COMMENT,
                        res: {
                            trendsid: req.trendsid,
                            comment: results,
                        }
                    });
                }
            }else {
                Toast.fail(message, 1);
            }
        })
        .catch((error) => {
            Toast.fail(error, 1);
        })
}

/**
 * 
 * @param roomID
 */
export function getQuanChengyuan(roomID) {
    return (dispatch, getState) =>{
        // if(!getState().dongtai.quan.roomID){
        //     return;
        // }
        // const req = {
        //     roomnum: getState().dongtai.quan.roomID,
        // };
        const req = {
            roomnum: roomID,
        };

        console.log("djy---getQuanChengyuan--" + req);
        return fetch(FetchUtil.getGetReq(APIs.GET_MUC_BODYS, req))
            .then((response) => response.json())
            .then((res) => {
                console.log(res);
                const {status, message, results} = res;
                if(status === 100){
                    dispatch({
                        type: CHANGE_QUAN_CHENGYUAN,
                        res: {
                            usercount: results == null ? 0 : results.length,
                        }
                    });
                }else {
                    Toast.fail(message, 1);
                }
            })
            .catch((error) => {
                Toast.fail(error, 1);
            })
    }
}



