import React, { PureComponent } from 'react'

class Divider extends PureComponent{
    render(){

        const {style} = this.props;
        return(
            <div style={style} className="divider"></div>
        );
    }
}
const styles = {
    divider: {
        marginTop: '0.15rem',
        marginBottom: '0.15rem',
    }
}

export default Divider;


