/**
 * 
 * @param timestr
 * @returns {string}
 */
export function getLocalTime(timestr) {
    if(timestr){
        let timeChuoInt = Number(timestr);
        let newDate = new Date();
        newDate.setTime(timeChuoInt * 1000);
        return newDate.toLocaleString();
    }
    return "";
}
/**
 * 
 * @param timestr
 * @returns {string}
 */
export function getAgoTime(timestr) {
    if(timestr){
        let diff = new Date().getTime()/1000 - Number(timestr);
        let day_diff = Math.floor(diff/86400); //

        return (day_diff == 0 && (
            diff < 60 && "" ||
            diff < 120 && "1" ||
            diff < 3600 && Math.floor(diff/60)+"" ||
            diff < 7200 &&"1" ||
            diff < 86400 && Math.floor(diff/3600)+""))||
            day_diff == 1 && "" ||
            day_diff < 7 && day_diff+"" ||
            Math.ceil(day_diff/7) + "";
    }
    return "";
}


