import {
    REFRESH_ING,
    GET_MY_CIRCLE,
    ERROR,
    TOGGLE_POP,
} from '../actions/ActionsTypes'

import {ListView} from 'antd-mobile'

const initState = {
    dataSource: new ListView.DataSource({
        rowHasChanged: (row1, row2) => row1 !== row2,
    }),
    refreshing: false,
    results:[],
    showPop: false,
    }

export default function quanzi(state = initState, action) {

    switch (action.type) {
        case REFRESH_ING:
            return Object.assign({}, state, {refreshing : true});
            break;
        case TOGGLE_POP:
            return Object.assign({}, state, {showPop : action.res.showPop});
            break;
        case ERROR:
            return Object.assign({}, state, {refreshing : false});
            break;
        case GET_MY_CIRCLE:
            const {results} = action.res;
            return Object.assign({}, state, {
                refreshing : false,
                results: results,
            });
            break;

        default:
            return state;
    }
}


