export function fetchPost(url ,data) {
    let dataUrl = '';
    if (data)  {
        for (let item in data)  {
            if (data.hasOwnProperty(item))  {
                dataUrl += ('&' + item + '=' + data[item]);
            }
        }
    }
    let body = dataUrl;
    return fetch (url, {
        method: 'POST',
        headers: {
            "Content-Type": "application/x-www-form-urlencoded"
        },
        body: body
    }).then(function(res) {
        if (res.status !== 200) {
            console.log('Looks like there was a problem. Status Code: ' + res.status);
            return;
        }
        // 
        return res.json();
    }).catch(function(err) {
        console.log('Fetch Error : %S', err);
    })
}

export function getPostReq(data) {
    let body = '';
    for (let item in data)  {
        if (data.hasOwnProperty(item))  {
            body += ('&' + item + '=' + data[item]);
        }
    }

    console.log("body--->" + body);
    return {
        method: 'POST',
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        body: body,
    };
}
export function getGetReq(url, data) {
    let index = 0;
    for (let item in data)  {
        if (data.hasOwnProperty(item))  {
            if(index === 0){
                url += ('?' + item + '=' + data[item]);
                index++;
            }else
                url += ('&' + item + '=' + data[item]);
        }
    }
    console.log("url--->" + url);
    return url;
}


